* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul{
  padding-left: 0;
}

/* navbar  */

.nav-bar {
  width: 100%;
  height: 200px;
  background: linear-gradient(180deg, rgba(213, 0, 249, 0.3) 0%, rgba(196, 196, 196, 0) 100%);
}

.nav-bar a {
  text-decoration: none;
  color: inherit;
}

.navbar-list {
  display: flex;
  list-style-type: none;
  float: right;
}

.navbar-list-item {
  text-transform: uppercase;
  padding: 30px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 33px;
  color: #BF00FF;
  transition: 200ms;
}

.navbar-list-item:hover {
  color: #ececec;
}

.hamburger{
  position: absolute;
  width: 46.8px;
  height: 26px;
  right: 10px;
  top: 20px;
  display: none;
}

.exodus-logo-navbar{
    font-family:  sans-serif;
    font-style: italic;
    font-weight: normal;
    text-align: center;
    color: #BF00FF;
    position: absolute;
    width: 170px;
    height: 64px;
    padding: 30px;
    font-size: 28px;
    line-height: 32px;
    letter-spacing: 0.12em;
    margin-left: 48px;
}

@media only screen and (max-width: 1200px) {

  .navbar-list-item {
    padding: 25px;
    font-size: 24px;
  }

  .exodus-logo-navbar{
    padding: 25px;
  }

}

@media only screen and (max-width: 992px) {

  .navbar-list-item {
    padding: 8px;
    font-size: 14px;
  }

  .nav-bar {
    height: 80px
  }

  .exodus-logo-navbar{
    font-size: 20px;
    padding: 8px;
  }

}

@media only screen and (max-width: 576px) {

  .hamburger{
    display: block;
  }

