/*Animation*/

:root {
--src: url(/static/media/home-page-image.7c69e02e.jpeg);
--overlay-color: rgb(191, 0, 255);
border-radius: 15px;
--size: 80vmin;
--space: 8vmin;
--duration: 300ms;
--ease-out: cubic-bezier(0.25, 1, 0.5, 1);
--bounce-out: cubic-bezier(0.34, 1.56, 0.64, 1);
}

.promo {
position: relative;
cursor: pointer;
width: 80vmin;
width: var(--size);
/* height: 150vh; */
--overlay-color: hotpink;
margin-top: -460px;
margin-left: 55px;
border-radius: 100px;
}

.image-wrapper {
width: 80vmin;
width: var(--size);
height: 80vmin;
height: var(--size);
/* overflow: hidden; */
-webkit-clip-path: polygon(100% 0, 100% 50%, 100% 100%, 0% 100%, 0 50%, 0% 0%);
        clip-path: polygon(100% 0, 100% 50%, 100% 100%, 0% 100%, 0 50%, 0% 0%);
transition: -webkit-transform 300ms cubic-bezier(0.25, 1, 0.5, 1),
-webkit-clip-path 300ms cubic-bezier(0.25, 1, 0.5, 1);
transition: transform 300ms cubic-bezier(0.25, 1, 0.5, 1),
clip-path 300ms cubic-bezier(0.25, 1, 0.5, 1);
transition: transform 300ms cubic-bezier(0.25, 1, 0.5, 1),
clip-path 300ms cubic-bezier(0.25, 1, 0.5, 1),
-webkit-transform 300ms cubic-bezier(0.25, 1, 0.5, 1),
-webkit-clip-path 300ms cubic-bezier(0.25, 1, 0.5, 1);
transition: -webkit-transform var(--duration) var(--ease-out),
-webkit-clip-path var(--duration) var(--ease-out);
transition: transform var(--duration) var(--ease-out),
clip-path var(--duration) var(--ease-out);
transition: transform var(--duration) var(--ease-out),
clip-path var(--duration) var(--ease-out),
-webkit-transform var(--duration) var(--ease-out),
-webkit-clip-path var(--duration) var(--ease-out);
 } 

.image-wrapper img {
position: relative;
width: 120%;
height: 100%;
object-fit: cover;
-webkit-transform: translateX(-10%);
        transform: translateX(-10%);
transition: -webkit-transform 300ms cubic-bezier(0.25, 1, 0.5, 1);
transition: transform 300ms cubic-bezier(0.25, 1, 0.5, 1);
transition: transform 300ms cubic-bezier(0.25, 1, 0.5, 1), -webkit-transform 300ms cubic-bezier(0.25, 1, 0.5, 1);
transition: -webkit-transform var(--duration) var(--ease-out);
transition: transform var(--duration) var(--ease-out);
transition: transform var(--duration) var(--ease-out), -webkit-transform var(--duration) var(--ease-out);
}  

.image-wrapper::after {
content: "";
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
background-color: rgb(191, 0, 255);
background-color: var(--overlay-color);
